import React, { useState } from 'react'
import Orb from '../Orb'
import Ring from '../Ring'
import Circle from '../Circle'
import Spinner from '../Spinner'

const SocialMedia = ({
  xy,
  initialScale = 1
}) => {
  const [ scale, setScale ] = useState(initialScale)
  const CIRCLE_SIZE = 50
  const RING_SIZE = CIRCLE_SIZE * 3

  const a = 360 / 3
  const angles = [ (a * 0), (a * 1), (a * 2) ]

  return (
    <Orb
      xy={xy}
      scale={scale}
      setScale={setScale}
      className="social-media"
    >
      <Ring
        size={RING_SIZE}
        hidering
      >
        <Circle
          text="github"
          link="https://github.com/coltonehrman/"
          angle={angles[0]}
          size={CIRCLE_SIZE}
          ringRadius={RING_SIZE / 2}
          spin
        />

        <Circle
          text="linkedin"
          link="https://www.linkedin.com/in/colton-ehrman/"
          angle={angles[1]}
          size={CIRCLE_SIZE}
          ringRadius={RING_SIZE / 2}
          spin
        />

        <Circle
          text="dev.to"
          link="https://dev.to/coltonehrman"
          angle={angles[2]}
          size={CIRCLE_SIZE}
          ringRadius={RING_SIZE / 2}
          spin
        />

        <Spinner
          scale={0.75}
          upperText="CONNECT WITH ME"
          lowerText="SOCIAL MEDIA LINKS"
          spin
        />
      </Ring>
    </Orb>
  )
}

export default SocialMedia
