import React, { useState } from 'react'
import Spinner from '../Spinner'
import Orb from '../Orb'
import InnerRing from './InnerRing'
import RingTwo from './RingTwo'
import './styles.css'

const Projects = () => {
  const hovering = [ useState(false), useState(false) ]
  const BASE_CIRCLE_SIZE = 50

  return (
    <Orb className="projects">
      <RingTwo
        circleSize={BASE_CIRCLE_SIZE * 1}
        hovering={hovering[0][0]}
        onMouseEnter={() => {
          hovering[0][1](true)
          hovering[1][1](false)
        }}
        onMouseLeave={() => hovering[0][1](false)}
      >
        <InnerRing
          circleSize={BASE_CIRCLE_SIZE}
          hovering={hovering[1][0]}
          onMouseEnter={() => {
            hovering[1][1](true)
            hovering[0][1](false)
          }}
          onMouseLeave={() => {
            hovering[1][1](false)
            hovering[0][1](true)
          }}
        >
          <Spinner
            upperText="PROJECTS"
            lowerText="PROJECTS"
          />
        </InnerRing>
      </RingTwo>
    </Orb>
  )
}

export default Projects
